.App {
  width: 100%;
  height: 100%;
}

body {
  background-color: #0C0D11;
}

form {
  background-color: #111217;
  padding: 40px;
  border-radius: 15px;
}

.elementContainer {
  margin: 20px;
}

#subBtn {
  margin-right: 20px;
}

label {
  color: #7F86A6 !important;
}

input, select {
  color:  #7F86A6 !important;
}

svg {
  color: #7F86A6 !important;
}

.MuiOutlinedInput-notchedOutline {
  border-color: #292C37 !important;
}

.MuiOutlinedInput-root.Mui-error .MuiOutlinedInput-notchedOutline {
  border-color: red !important;
}

.MuiSlider-root {
  color: #BC3756 !important;
}

#time::-webkit-calendar-picker-indicator {
  filter: invert(0.8);
}

.MuiButton-contained.Mui-disabled {
  color: rgba(255, 255, 255, .5) !important;
}

.alert {
  position: fixed;
  width: 80%;
  top: 10px;
  left: 10%;
  z-index: 2;
}

.dishes {
  height: 100%;
  overflow: auto;
}

.dishes::-webkit-scrollbar-track
{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  border-radius: 10px;
  background-color: #F5F5F5;
}

.dishes::-webkit-scrollbar
{
  width: 8px;
  background-color: #F5F5F5;
}

.dishes::-webkit-scrollbar-thumb
{
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
  background-color: #555;
}

.dish {
  background-color: #111217;
  color: #7F86A6;
  margin: auto;
  padding: 10px;
  margin: 10px;
  border-radius: 10px;
  font-size: 10px;
}

.dish:nth-of-type(2n) {
  background-color: red;
}

@media (max-width: 538px) {

  .dishes {
  height: 50%;
  }
}